* {
  margin: 0;
}

html {
  width: 100%;
}

.blocks {
  display: flex;
  flex-wrap: wrap;
  justify-content: reverse;
}

.block {
  height: 300px;
  min-width: 300px;
  flex-grow: 1;
  flex-shrink: 0;
  font-family: Arial, Helvetica, sans-serif;
  color: white;
  font-size: 100px;
  text-align: center;
  line-height: 300px;
}

.block:nth-child(1) {
  background-color: #f00;
}

.block:nth-child(2) {
  background-color: rgb(80%, 0%, 0%);
}

.block:nth-child(3) {
  background-color: rgb(60%, 0%, 0%);
}

.block:nth-child(4) {
  background-color: rgb(40%, 0%, 0%);
}

.block:nth-child(5) {
  background-color: rgb(20%, 0%, 0%);
}

.block:nth-child(6) {
  background-color: black;
}

@media screen and (max-width: 900px) {
  .block:nth-child(3),
  .block:nth-child(5),
  .block:nth-child(6) {
    order: 2;
  }
}

@media screen and (min-width: 900px) {
  .block {
    min-width: 30%;
  }
  .block:nth-child(4) {
    order: 4;
  }
  .block:nth-child(5) {
    order: 3;
  }

}

@media screen and (max-width: 599px) {
  .block {
    order: 2;
  }
}
